<template>
  
    <div class="relative" >
     
      <div class="bg-white">
      <div  class="border-blue-500 text-xl text-gray-500 font-black rounded px-6 py-1 ">
         Register
      </div>
      
        <div class="mt-1 float-right relative logpat">
            <a href="#" class="z-10 hover:text-white hover:bg-blue-500  border-blue-500 text-xs rounded px-3 py-1 leading-normal text-yellow-500"
              @click="logstate()">Log in           
          </a>
          </div>            
         </div>
         

      <form  class="sfrm loginback" :class="{'stepin': logNext==0 }">
          <div class="mt-2 text-xs text-yellow-100">www.wemakeway.co.za</div>
        <div class="frm5 mt-3 ">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="firstname"
            type="text"
            v-model="useData.params.rbufname"
            required="required"
          />
          <label for="firstname" class="mXX pt-1 mt-1">
            <span id="sp1" class="fa fa-user"></span>&nbsp;|&nbsp;First names
          </label>
        </div>
        <div class="frm5">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="surname"
            type="text"
            v-model="useData.params.rbusname"
            required="required"
          />
          <label for="surname" class="mXX pt-1 mt-1">
            <span id="sp1" class="fa fa-user"></span>&nbsp;|&nbsp;Surname
          </label>
        </div>

        <div class="frm5">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="email"
            type="text"
            v-model="useData.params.rbuemail"
            required="required"
          />
          <label for="email" class="mXX pt-1 mt-1">
            <span id="sp1" class="fa fa-envelope"></span>&nbsp;|&nbsp;Email
          </label>
        </div>
        <div class="frm5">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="connum"
            type="text"
            v-model="useData.params.rbucontact"
            required="required"
          />
          <label for="connum" class="mXX pt-1 mt-1">
            <span id="sp1" class="fa fa-phone"></span>&nbsp;|&nbsp;Contact No.
          </label>
        </div>
        <div class="m30">
          <a
            href="#"
            class="hover:text-white hover:bg-blue-500 border-blue-500 text-ms rounded px-8 py-1 leading-normal"
            @click="checkform"
          >
            Next
            <span class="fa fa-chevron-circle-right p-1"></span>
          </a>
        </div>
      </form>
   
    <div class="loginback2" :class="{'stepin': logNext==1}">
      <div class="mt-2">Authentication details</div>
      <form class="sfrm ">
        <div class="flex">
          
        </div>
        <div class="frm5 mt-3">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="username"
            type="text"
            v-model="useData.params.rbuuname"
            required="required"
          />
          <label for="username" class="mXX pt-1 mt-1">
            <span id="spuser" class="fa fa-user"></span>&nbsp;|&nbsp;Username
          </label>
        </div>
        <div class="frm5 mt-1">
          <div class="iconshow" v-if="showico">
            <div
              class="pt-1 pl-2 rounded fas fa-eye-slash passstr"
              :class="'level_' + passscore"
              v-if="showc"
              @click="showc=!showc"
            ></div>
            <div
              class="pt-1 pl-2 rounded fas fa-eye passstr"
              :class="'level_' + passscore"
              v-else
              @click="showc=!showc"
            ></div>
          </div>

          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="password"
            @keyup="checkpass"
            :class="{'padin': showico==true,'padout': showico==false}"
            :type="showc=== true ? 'text' : 'password'"
            v-model="useData.params.rbupass"
            required="required"
          />
          <label for="password" class="mXX pt-1 mt-1">
            <span id="sppass1" class="fa fa-user-secret"></span>&nbsp;|&nbsp;Password
          </label>
        </div>
        <div class="frm5 mt-1">
          <input
            class="inputbox shadow-md pt-1 pl-2 rounded"
            id="repassword"
            type="password"
            v-model="passcon"
            required="required"
          />
          <label for="repassword" class="mXX pt-1 mt-1">
            <span id="sppass2" class="fa fa-user-secret"></span>&nbsp;|&nbsp;confirm Password
          </label>
        </div>
        <div class="frm65 mt-1">
          <div class="mt-1 m28">
            <a
              href="#"
              class="hover:text-white hover:bg-blue-500 border-blue-500 text-ms rounded px-6 py-1 leading-normal"
              @click="logNext=0"
            >
              <span class="fa fa-chevron-circle-left p-1"></span>
              Back
            </a>
          </div>
          <div class="mt-1 m28">
            <a
              href="#"
              class="hover:text-white hover:bg-blue-500 border-blue-500 text-ms rounded px-6 py-1 leading-normal"
              @click="addUser"
            >
              Next
              <span class="fa fa-chevron-circle-right p-1"></span>
            </a>
          </div>
        </div>
      </form>
    </div>
    <div class="loginback3" :class="{'stepin': logNext==2 }">
      <div class="mt-10 text-white frm66">Congradulations....</div>

      <div class="mt-50 m28">
        <a
          href="#"
          class="hover:text-white hover:bg-blue-500 border-blue-500 text-ms rounded px-8 py-1 leading-normal"
          @click="logNext=0,$emit('finishlog')"
        >
          <span class="fa fa-chevron-circle-left p-1"></span>Continue to re`Business
          <span class="fa fa-chevron-circle-right p-1"></span>
        </a>
      </div>
    </div>
   </div>
   
</template>
<script>
//import axios from "axios";
//import swal from "sweetalert";
//import router from '../router.js';
import {mapState, mapMutations} from 'vuex'
export default {
  computed: mapState([
      'checksate'
  ]),
  name: "logreg",
  data: () => {
    return {
      logNext: 0,
      startnow: true,
      passscore: 0,
      showc: false,
      showico: false,
      passcon: "",
      pass: "",
      useData: {
        params: {
          rbufname: "",
          rbusname: "",
          rbuemail: "",
          rbucontact: "",
          rbuuname: "",
          rbupass: "",
          rbutype: "",
          rbuscheme: 0,
          rbuvest: 0,
          creat_time: new Date().toLocaleString()
        }
      },
      passtreng: ["Very weak", "Weak", "Medium", "Strong"]
    };
  },

  methods: {
     ...mapMutations([
         'CHANGE_LOGSTATE'
    ]),
    logstate:function(){
        //alert("king Pie");
         this.CHANGE_LOGSTATE(!this.checksate);
         
    },
    gethis(){
      alert("King pin");
    },
    checkform() {
      if (this.useData.params.rbufname === "") {
        alert(`Missing Firstname`, "Please enter your name", "error");
        return;
      }
      if (/\W/.test(this.useData.params.rbufname) || (/\d/.test(this.useData.params.rbufname))){
         alert(`Invalid characters detected in the Name area, `, "Please enter a valid name", "error");
        return;
      }       
      if (this.useData.params.rbusname === "") {
        alert(`Missing Surname`, "Please enter your surname", "error");
        return;
      }
      if (/\W/.test(this.useData.params.rbusname) || (/\d/.test(this.useData.params.rbusname))){
         alert(`Invalid characters detected in the Surname area`, "Please enter a valid surname", "error");
        return;
      }    
      if (
        !/^([A-Za-z0-9_\-.+])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/.test(
          this.useData.params.rbuemail
        )
      ) {
        alert(`Missing/Invalid email`, "Please enter email address", "error");
        return;
      }
      if (this.useData.params.rbucontact === "") {
        alert(`Missing Contact No.`, "Please enter your surname", "error");
        return;
      } else {
        var pchk = this.useData.params.rbucontact;
        var cscore = 0;
        if (/\W/.test(pchk)) cscore = 1;
        //if (/\d/.test(pchk)) cscore += 0;
        if (/[a-z]/.test(pchk)) cscore = 1;
        // Contains upper case letter
        if (/[A-Z]/.test(pchk)) cscore = 1;
        if (cscore === 1) {
          alert(
            `Invalid contact number`,
            "Please only enter numbers, don't include spaces",
            "error"
          );
          return;
        }
      }
      
      this.logNext = 1;
    },

    nextStep() {
      this.animation = "animate-out";
      setTimeout(() => {
        this.animation = "animate-in";
        this.activeStep += 1;
      }, 550);
    },
    switchstates() {
      this.loginze = true;
      this.logop = true;
    },
    checkpass() {
      this.passscore = 0;
      if (this.useData.params.rbupass === "") {
        this.showico = false;
        this.showico = false;
        return;
      } else {
        this.showico = true;
        var pchk = this.useData.params.rbupass;
        if (/\W/.test(pchk)) this.passscore += 20;
        if (/\d/.test(pchk)) this.passscore += 20;
        if (/[a-z]/.test(pchk)) this.passscore += 20;

        if (/[A-Z]/.test(pchk)) this.passscore += 20;

        if (pchk.length >= 6) this.passscore += 20;

      }
    },

    addUser() {
      var rec = this.useData.params.rbupass;
      var reccon = this.passcon;
      if (rec !== reccon) {
        alert(`Passwords don't match`, "Please re-enter password", "error");
        this.passcon = "";
        return;
      }
    
    }
  }
};
</script>
<style lang="scss" scoped>
.frmcont {
  width: 100%;
  }
.loginback {
  position: absolute;
  background-color: rgba(14, 13, 13, 0.596);  
  width: 0%;
  opacity: 0;
  z-index: 100;
  left: -100%;
  transition: all 1s;
}
.loginback2 { 
  position: absolute;
  background-color: rgba(14, 13, 13, 0.596);  
  width: 0%;
  opacity: 0;
  z-index: 100;
  overflow: hidden;
  left: -100%;
  transition: all 1s;
  backface-visibility: hidden;
}

.loginback3 {
  position: absolute;
  background-color: rgba(14, 13, 13, 0.596);
  width: 0%;
  opacity: 0;
  z-index: 100;
  overflow: hidden;
  left: -100%;
  transition: all 1s;
  backface-visibility: hidden;
}
.stepin {
  width: 100%;
  left: 0%;
  opacity: 1;
}
.sfrm {
  height: calc(100%-20%);
 
  color: rgba(27, 27, 27, 0.651);
}
.frm66 {
  overflow: hidden;
}
.logpat{
  z-index: 300;
}
.padin {
  padding: 0px 30px 0px;
}
.padout {
  padding: 0px 10px 0px;
}
#password {
  transition: all 1s;
}
.eyeshide {
  display: none;
}
.eyesshow {
  display: block;
}
input {
  width: 100%;
  left: 0px;
}
input ~ label {
  padding-top: 2px;
  width: 100%;
  left: 0px;
  transition: all 1s;
  font: 14px Roboto;
}
input:valid ~ label {
  padding-top: 2px;
  left: 68%;
  font: 12px Roboto;
  color: #f75308;
}
input:valid {
  width: 71%;
  transition: all 0.5s;
}
input:focus {
  outline: none;
}
input:focus ~ label {
  left: 270px;
  font: 12px Roboto;
  color: #939469;
}

input:focus ~ .bar:before {
  transform: translateX(0);
}
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px #333 inset;
  -webkit-text-fill-color: white !important;
  box-shadow: 0 0 0px 1000px #333 inset;
}
</style>