import { createStore } from 'vuex'

export default createStore({
  state: {
     checksate:true,
     loginchk:false,
  },
  mutations: {
   
    CHANGE_LOGSTATE:(state,lstate)=>{
      state.checksate= lstate;
    },
    GUARD_LOG:(state,lstate)=>{
      state.loginchk= lstate;
    }
  },
  actions: {
  },
  modules: {
  }
})
