    created () {
      this.takePulse(false)
    },
    methods: {
      heartbeat () {
        return Math.ceil(Math.random() * (120 - 80) + 80)
      },
      async takePulse (inhale = true) {
        this.checking = true
        inhale && await exhale(1000)
        this.heartbeats = Array.from({ length: 20 }, this.heartbeat)
        this.checking = false
      }
